			recipe: 'Приготовьте начинки для макарунов. Для лимонной начинки: смешайте лимонную цедру с сахаром. Добавьте к ним яйца и сок и перемешайте. Миску со смесью поставьте на водяную баню. Нагревайте, постоянно помешивая, до 84C (около 15 мин). Как только крем загустеет, снимите миску с огня, дайте остыть до 60C, добавьте кусочки сливочного масла и взбейте миксером, чтобы масса стала однородной. Накройте пищевой плёнкой и поставьте в холодильник на ночь. Для начинки с шоколадом: шоколад поломайте на мелкие кусочки, сливки доведите до кипения, постоянно помешивая. Как только сливки закипят, снимите их с огня и добавьте шоколад. Смесь хорошо перемешайте до однородной массы (можно взбить миксером), остудите в холодильнике в течение 10-12 часов. Для начинки с маскарпоне и сгущенкой: взбейте маскарпоне и постепенно добавляйте сгущенное молоко, продолжая взбивать, до достижения необходимой консистенции. Приготовьте макаруны. Разогрейте духовку до 140 С. Смешайте миндальную муку и сахарную пудру. Просейте. Из воды и 150 г сахара сварите сироп до 114-116 С. Белки разделите на две равных части по 55 г. Одну их них влейте в смесь муки и пудры, вторую взбейте с оставшимися 15 г сахара до мягких пиков. Продолжая взбивать, влейте тоненькой струйкой сироп в белки. Взбивайте пока меренга не остынет до 40С. Перемешайте сахарную пудру, миндальную муку и белки до однородной консистенции. Добавьте пищевой краситель, если необходимо. В 3 приёма вмешайте меренгу до такой консистенции, чтобы масса не спадала с ложки куском, а как бы стекала лентой (однако, также важно не перемесить, чтобы она не стала жидковатой и слишком растекающейся). Выложите получившуюся смесь в кулинарный мешок с круглой насадкой 8 мм. Отсадите заготовки диаметром 4 - 4,5 см. 13 -15 мин (в зависимости от духовки). Полностью остудите, снимите с противня и склейте половинки с помощью начинок.'
