			recipe: 'Разогрейте духовку до 200 °С. Испеките бисквит для основы пирожного Картошка. Взбейте яйца с сахаром в пышную светлую пену при помощи миксера, 10 мин. Аккуратно­ вмешайте просеянную муку и соль. Выложите­ тесто на застеленный бумагой для выпечки противень и выпекайте 13–15 мин. Остудите бисквит и оставьте подсыхать на 8–24 ч, затем наломайте кусочками и положите в кухонный комбайн. Измельчите в крошку, которая станет основой пирожного Картошка. Для крема пирожного Картошка взбейте масло с ванильным сахаром до пышности. Продолжая взбивать, постепенно добавьте сгущенное молоко. Отложите 2 ст. л. крема для украшения. Оставшийся крем смешайте с бисквитной крошкой и ромом/коньяком до однородности. Из получившейся массы слепите 12 пирожных в форме продолговатой картофелины. Обваляйте в какао-порошке. Кончиком китайской палочки для еды сделайте в каждой «картошке» по 2 углубления. Уложите крем в «фунтик» из бумаги и выдавите в каждое углубление «ростки». Положите пирожные Картошка в холодильник минимум на 1 ч и подавайте.'
