			recipe: 'Для начала приготовим песочные Корзиночки. Масло взбить, добавить сахар, взбивать до растворения сахара. Добавить 2 яйца (по одному) и взбить, добавить соду и ванилин. Просеять муку и замесить гладкое, мягкое тесто. Убрать тесто в морозильник на 5-7 минут. После того, как тесто остыло разделить его на равные шарики по 18-25 г. Взять формочки для кексов и аккуратно, ровным слоем распределить тесто (руками, раскатывать не нужно). Выпекать в разогретой духовке до 180-190°C около 10-13 минут. После чего, корзиночки достать и остудить. 500 мл жирных сливок взбить с сахарной пудрой до устойчивых пиков. Теперь берем наши корзиночки, в каждую корзиночку кладем по 1.5-2 ч.л джема, наносим с помощью кондитерского мешка или ложки взбитые сливки. Теперь берем ягоды и украшаем по желанию. Я брала клубнику, голубику и ежевику. Клубнику нарезала на тонкие кружочки и прикрепляла, так скажем, по бокам корзиночки. В центр можно положить голубику и веточку мяты.'
