			recipe: 'Печем кексы. Взбить сливочное масло с сахаром, солью и ванилином. Поочередно добавить яйца. Одно добавили — взбили до однородности, второе добавили — взбили. Затем добавить молоко и муку с разрыхлителем. Часть молока, часть муки, потом снова молоко и завершить мукой. Взбить.Тесто выложить в бумажные формочки на 2/3 высоты. Выпекать в заранее разогретой до 180°С духовке примерно 25 минут. Готовность проверить шпажкой. Сухая, кексы готовы.Пока пекутся кексы, варим кофе, даем остыть. Как только испекутся кексы, не вынимая их из формы, делаем проколы, по 10–12 штук протыкаем каждый кекс.Пропитываем кексы кофе с ромом. Можно с помощью чайной ложки, я делала это с помощью кисточки. Пропитки не жалейте. Затем кексам даем полностью остыть.Готовим крем. Для этого взбиваем маскарпоне. Отдельно взбиваем сливки с сахарной пудрой и ванилином, до мягких пиков, не перевзбейте. Аккуратно смешайте маскарпоне и сливки. С помощью кондитерского мешка выложите крем на кекс, если мешка нет, можно крем положить в целлофановый пакетик, отрезать уголок и таким образом сделать кондитерский мешочек. Сверху кексы посыпать какао. Можно наслаждаться прекрасными кексами. Поверьте, они прекрасны, очень вкусные! Приготовив их однажды, вам непременно захочется готовить их снова! Приятного аппетита!'
