			recipe: 'Разогреть духовку до 180 градусов. Застелить формочки для кексов бумажными вкладышами. Перемешать муку, какао и соль. Взбить сахар и масло миксером. Добавить яйца, по одному за раз, взбивая после каждого добавления. Добавить пищевой краситель и ваниль. Уменьшить скорость до низкой. Добавить мучную смесь в три приема, чередуя с двумя добавлениями кефира, и взбивая после каждого. Смешать пищевую соду и уксус в небольшой миске (она будет пениться), добавить смесь в тесто и взбивать на средней скорости 10 секунд. Разделить тесто равномерно между формочками, заполняя каждую на три четверти. Выпекать около 20 минут. Остудить на решетке. Взбить все ингредиенты для крема отправить в кондитерский мешок и выдавить на кексы. Глазурь также можно намазать ложкой. Отправить капкейки в холодильник на 2 часа перед подачей.'
