			recipe: 'Приготовить тесто. Смешать молоко с половиной сахара и небольшим количеством муки. Добавить дрожжи. Накрыть и оставить на 1 ч. Всыпать оставшуюся муку, сахар, соль, сливочное масло, яйцо и желток. Вымесить тесто. Накрыть и оставить еще на 1,5 ч. 2. Приготовить начинку. Мак промыть теплой водой, пересыпать в миску, ошпарить крутым кипятком и сразу же слить воду. Еще раз залить кипятком и дать настояться не больше 1 мин. Слить воду, миску с маком накрыть. 3. Вскипятить молоко вместе с сахаром. Добавить ванильную эссенцию и желток. Проварить, постоянно помешивая, 2 мин. Снять с огня, дать остыть. 4. Добавить распаренный мак, творог и апельсиновую цедру. Тщательно перемешать. 5. Приготовить обсыпку для пирога. Смешать просеянную муку, сахар, желток, масло и корицу. Отставить. Тесто обмять, выложить на присыпанный мукой стол и раскатать в пласт толщиной 5 мм. Выложить в форму, хорошо смазанную маслом. 6. Разогреть духовку до 180ºС. На тесто выжить маковую начинку. Сверху ровным слоем распределить обсыпку. Подравнять края теста, завернуть их внутрь. Поставить в духовку и выпекать 30 мин.'
