			recipe: 'Приготовить тесто. Взбить яйца с сахаромРазмягчить масло, смешать его со сметаной и яично-сахарной смесью. Постоянно помешивая, всыпать муку и разрыхлитель и замесить однородное тесто. Накрыть, убрать в холодильник на 20–30 мин. Разъемную форму для выпечки диаметром 26 см смазать маслом, посыпать тонким слоем манной крупы. Тесто выложить в форму и руками распределить по всей поверхности. Сделать по краям небольшой бортик. Слегка припылить тесто мукой. Ягоды вымыть, обсушить бумажными полотенцами, выложить на тесто и посыпать коричневым сахаром.Приготовить крем. Смешать йогурт со сметаной, яйцом и сахаром. Взбить ингредиенты в однородную пышную массу.Разогреть духовку до 200 °С. Распределить крем по поверхности ягод. Пирог поставить в духовку и выпекать 30–35 мин. Подать к столу теплым.'
