			link: 'semolina-pie',
			ingredients: ['манная крупа 125 г', 'рикотта 250 г', 'яйца куриные 3 шт.', 'масло сливочное 70 г', 'сахар 180 г', 'цедра лимона 1/2 ч. л.', 'цедра апельсина 1/2 ч. л.', 'лимончелло (ликер) 1 ч.л.', 'молоко 500 мл'],
			recipe: 'В кастрюле смешать молоко, масло и цедру апельсина и лимона. Довести до кипения. Ввести постепенно манную крупу, постоянно помешивая. Как только все молоко впитается в крупу, снять с огня, переложить в чашку и полностью остудить. Яйца взбить с сахаром, чтоб масса посветлела. Добавить рикотту, перемешать миксером. Добавить остывшую манную кашу. Добавить лимончелло (я добавила лимонный экстракт и 1 ст.л. коньяка). Перемешать миксером. Форму 20 см смазать маслом и присыпать мукой. Выложить тесто в форму. Духовку разогреть до 180 градусов, печь пирог 45-50 мин. до тех пор, пока деревянная шпажка не будет чистой. Не пересушите пирог. Если верх зарумянится раньше времени, накрыть фольгой.'
