			link: 'oatmeal-cookie',
			ingredients: ['грецкие орехи – небольшая горсть', 'овсяные хлопья (лучше быстрого приготовления) – 2 стакана', 'мука – 2 ст. л.', 'ванильный сахар – 1 ч. л.', 'масло сливочное – 100 г', 'сахар – 1 стакан', 'яйца – 2 шт', 'сахарная пудра для украшения', 'шоколад для украшения'],
			recipe: 'Грецкие орехи мелко порубите. Овсяные хлопья измельчите в блендере в муку. Пересыпьте их в миску, добавьте просеянную муку, ванильный сахар и грецкие орехи. Перемешайте. Взбейте миксером размягченное сливочное масло с сахаром до получения абсолютно однородной массы белого цвета. Продолжая взбивать, аккуратно влейте яйца. Затем добавьте просеянную муку с орехами, замесите тесто. Выложите тесто на присыпанную мукой рабочую поверхность и раскатайте в пласт толщиной 1 см. С помощью формочек вырежьте из теста различные фигурки. Положите их на противень, накрытый промасленным пергаментом, и выпекайте в разогретой до 180 °С духовке примерно 20 мин. Горячее печенье украсьте сахарной глазурью (см. совет) или растопленным шоколадом. Присыпьте овсяными хлопьями.'
