			recipe: 'Муку соединить с какао-порошком, разрыхлителем и ванильным сахаром (1 пакетик). Перемешать.90 г сливочного масла смешать с сахаром.Взбить миксером и добавить 1 яйцо. Перемешать.Смесь сухих ингредиентов соединить с масляно-яичной смесью. Перемешивать силиконовой лопаткой.Замесить тесто.Разделить тесто на 2 части. Завернуть в пищевую пленку и распластать, чтобы оно было толщиной с шоколадную плитку. Отправить в холодильник на 30 минут.Масло сливочное (25 г) смешать с сахарной пудрой.Добавить ванильный сахар и 25 мл молока. Размешать.Взбить миксером.Приготовленным кремом наполнить кондитерский мешок. Отправить в холодильник.Тесто выложить на пергаментный лист, сверху накрыть вторым листом и раскатать скалкой.Стаканом выдавить кружочки.Выложить заготовки для шоколадного печенья на противень, застеленный пергаментной бумагой.Отправить печенье в духовку, разогретую до 170 градусов, на 10 минут.Перевернуть шоколадное печенье и сверху выложить крем.Накрыть вторым печеньем.Домашнее печенье «Орео» готово. Всем приятного аппетита!'
