			recipe: 'На дно разъемной формы для выпечки диаметром 24 см положите пергаментный круг, стенки выстлите пергаментной лентой, ширина которой должна быть на 2 см больше высоты формы. Печенье раскрошите, добавьте сливочное масло и взбейте миксером в однородную массу. Выложите в форму, разровняйте поверхность и поставьте в разогретую до 180 °С духовку на 10 мин. Выньте, остудите (духовку не выключайте). Шоколад мелко порубите. Половину шоколада растопите на водяной бане. Остудите до комнатной температуры. Взбейте миксером сливочный сыр со сметаной и сахаром. Продолжая взбивать, добавьте яйца по одному. Добавьте растопленный и порубленный шоколад, влейте ликер, перемешайте. Перелейте получившуюся смесь в форму. На нижнюю полку духовки поставьте огнеупорную емкость, наполненную водой. На среднюю решетку поставьте форму с чизкейком. Готовьте 1 ч. Выключите духовку, откройте дверцу и дайте чизкейку полностью остыть. Поставьте в холодильник минимум на 4 ч. Перед подачей украсьте шоколадной стружкой, ягодами и взбитыми сливками.'
