			url: 'https://yandex-images.clstorage.net/cTR97d415/cb78bbb3U/a5s4AvUMtCjbe6t50IJVir-zvlGFJ-2LOQ1EvW-tpoTHj0xoI8N_79tLj-5yLyTvy8CTxpOEYjnXNFtekeXhtTaGZTPFYM0U83-rdeSmOa6jn55tpTe9lzBsGNAjkIaJi-pVCCpjhidX03tev9rZFj-0hrfexA5E3iny-qRgkOSnl6xKsQRZjH8vVslwMqkuT4IacRlKhXplCb0IK5iHqV-adOXah2Yf5xvLen_28e5jYMJL9viznIr5ymcI-ER8Syu4fmUcMUDr29ZtkOJ1tuNKzqE5Kn1ydZHd5I-0b4EvwphljiO2uxcLgzI7lhEuz7CeR5Kcc7wCMUrDMKi8iMdfEOLJEOGARy-20SA-VY5zZs5lre45ElWlDQyb5N95d25ARfYTYqvjz89mb055lqtE1rN6FE_M2sUy48y4jMi7E3SCIZC1KDPfrgUEFl1aoxqOoVGygX5hrX18xxTrYY86XBl60ybLM0cjblfiVY7DdD4bRghfuA7hxsegCGREy8f4nrVIwTC3y4bNOJZpDot2QkU1WukqwX3xCJMQ26HHukip8n8SHzNX087b0nWi5xieI4pksxiK3SIzgOig5I-rWGpZVCV4R6NuXage6Z4Hnp5FVRbhMpGhQWi_MJdBwzJIsYrDMuvLRxMmv6ql-uNwai-qhC_UfrUGC7Bo1My3X4jWaVRdnHt_Ysmwwnmew9rOuVVucfZNPW3sc_D_cUeW2K12p347a7tL0i-aVa5LtEJTCpA3bKK1WoMEFDDAY5OsRm1A6TTDV1LxDFZRuhvqNoH1QtWOWaUR4AfAi8n3mrSxLvfiYwO7x95Xlq0qM4yWl6Z4B1wu2U7DiDgcXH9LHEJhgPWwN1umkajS-b57ippRmdKNBqWFCYhrNAtpm1KIldarGvtny9dyk9ZtApuQBleOnI8kOqXKkzBwyPTD8yxyzYyZeDvPOn28MikKw2rSud3m_b4ZnUFskxRQ.jpg',
			title: 'Творожный чизкейк',
			link: 'sourcream-cheezecake',
			ingredients: ['100 г хорошего белого шоколада', '3 ст. л. жирных сливок', '50 г сдобного сухого печенья', '50 г очищенного миндаля', '300 г некислого творога средней жирности', '1 стакан жирныx сливoк', '150 г натурального йогуртa', '5 ч. л. мачча, зеленого чая в порошке', '1/2 стакана мелкокристаллического сахара', '10 г желатина', '3 ст. л. лимонного сока'],
			recipe: 'Для основы круглую форму диаметром 20–22 см облейте холодной водой и поставьте в морозильник на 5 мин. Миндаль поджарьте на сухой сковородке на среднем огне, потряхивая, 3–4 мин., и смелите в комбайне или в кофемолке в мелкую крошку. Печенье тоже измельчите в комбайне или поместите в пластиковый пакет и раскатайте скалкой. Смешайте печенье с миндалем. Порубите очень мелко шоколад, растопите его на водяной бане вместе со сливками. Добавьте печенье с миндалем, быстро перемешайте до однородности.Выньте форму из морозильника, застелите пленкой. Выложите шоколадную массу, уплотните и разровняйте руками, поставьте в холодильник, пока делаете начинку. Для начинки желатин замочите в 1/4 стакана холодной воды на 20 мин. Творог протрите через сито и миксером взбейте с сахаром. Йогурт смешайте с порошком зеленого чая. Добавьте йогурт в творог и опять взбейте. Желатин отожмите и, помешивая, растворите в лимонном соке на водяной бане. Влейте желатин в творожную массу и еще раз слегка взбейте. В отдельной миске взбейте очень холодные сливки и тщательно, лопаткой, вмешайте в творожную массу. Переложите начинку в форму с основой, разровняйте и поставьте в холодильник минимум на 4–5 ч. Для украшения растопите шоколад со сливками на водяной бане или в микроволновке, добавьте чайный порошок, перемешайте. Можете залить получившуюся массу в специальные формочки и получить зеленые листики, кружочки, треугольнички. Украсьте ими поверхность готового чизкейка.'
