			title: 'Черничный чизкейк',
			link: 'blueberry-cheezecake',
			ingredients: ['65 г кокосовой стружки', '120 г миндаля', '50 г кешью', '210 г фиников', '5 г ванилина', '¼ ч. л. соли', '260 г кешью', '120 г миндального молока', '10 г агар-агара', '5 г лимонного сока', '90 г меда', '5 г ванилина', '250 г кокосового масла', '300 г пюре из черники'],
			recipe: 'Измельчите миндаль, кешью и финики вместе с солью в блендере, добавьте ванилин и кокосовую стружку.Равномерно утрамбуйте полученную массу в разъемную форму для выпечки (или тарелку с высокими краями, проложив на дно пищевую пленку). Отправьте в блендер замоченный кешью, миндальное молоко, лимонный сок, мед, ванилин. Кокосовое масло слега подогрейте, чтобы оно начало медленно растапливаться.Добавьте агaр-агар и пюре из черники, перемешайте. Крем выложите поверх нижнего слоя и отправьте форму в морозилку, пока торт не затвердеет. За час до подачи десерта, переставьте торт из морозилки в холодильник. Острым ножом разрежьте на кусочки, и подавайте со свежими ягодами и вкусным чаем или лимонадом.'
